import React, { useState } from 'react'
import css from './Profile.module.css'

import { Helmet } from 'react-helmet'

import myFriends from '../../media/myfriends_entry.svg'
import loader from '../../media/loader200px.svg'

import iconSET from '../../media/SET.svg'

import io from 'socket.io-client'
import { Switch, Route } from 'react-router-dom'

import MyFriends from './MyFriends/Friends'
import Set from './SET/Set'

import FMenu from '../FMenu/Menu'
const socket = io('http://localhost:750')

const Default = (props: any) => {

  const [ storage, setStorage ]: any = useState(null)

  const getdata: any = {
    '__id': JSON.parse(localStorage.data)['__id'],
    'keys': ['user name', 'user id']
  }

  socket.emit('get', getdata, (data: any) => {

     setStorage({ name: data['user name'], id: data['user id'] })
  })

  return (
    <div className={css.area}>
      <Helmet>
        <title> Profile&ensp;&ensp;Shomi </title>
      </Helmet>
      <div className={css.friends}>
        <button className={css.tofriends}
                onClick={ () => props.history.push('/profile/friends') }>
          <img src={myFriends} alt='My friends' />
        </button>
      </div>
      <div className={css.blank}>
        { 
          (storage !== null) ?
          (
            <>
              <div className={css.user}>
                <svg className={css.icon} viewBox="0 0 320 320" width='105' height='105'>
                  <circle cx='160' cy='160' r='150'
                         fill="rgba(255, 238, 0, 0.9)" />
 
                  <text x="123" y="225" fill='#c3a720'
                        style={{
                            fontFamily: 'Lemonada',
                            fontWeight: 600,
                            fontSize: '160px'
                        }}> ? </text>

                </svg>
              <div className={css.userInfo}>
                <div className={css.username}>
                  {
                    (window.location.pathname === '/profile/edit') ?
                    (
                      <>
                        <input type="text" placeholder={storage.name} />
                        <span>#</span>
                        <input type="text" placeholder={storage.id} />
                      </>                      
                    ):

                    <span> { storage.name }<span>#{ storage.id }</span> </span>    
                  }
                </div>
                <div className={css.profileTo}>
                  {
                    (window.location.pathname !== '/profile/edit') ?
                    (
                      <>
                        <button className={css.edit}
                                onClick={ () => props.history.push('/profile/edit') }>
                          <span> Edit </span>
                        </button>
                        <button className={css.set}
                                onClick={ () => props.history.push('/profile/SET') }>
                          <img src={iconSET} alt='SET' width='25' />
                        </button>
                      </>
                    ):

                    (
                      <>
                        <button className={css.cancel}
                                onClick={ () => props.history.push('/profile') }>
                          <span> cancel </span>
                        </button>
                        <button className={css.submit}>
                          <span> apply </span>
                        </button>
                      </>
                    )
                  }
                </div>
              </div>
            </div>
            <div className={css.panel}>
              <div className={css.innerPanel}>
                <button className={css.live}
                        onClick={ () => props.history.push('/live') }>
                  <span className={css.innerLive}> LIVE </span>
                </button>
                <button className={css.dark}
                        onClick={ () => props.history.push('/live/query') }>
                  <span> Dark query </span>
                </button>
              </div>
            </div>
          </>
          ):
          (
            <img className={css.loaderGetData} src={loader} alt='load' />
          )
        }
        <FMenu />
      </div>
    </div>
  )
}


function Profile () {

  return (
    <Switch>
      <Route exact path='/profile' component={Default} />
      <Route exact path='/profile/edit' component={Default} />
      <Route path='/profile/friends' component={MyFriends} />
      <Route path='/profile/SET' component={Set} />
    </Switch>
  )

}

export default Profile
