import React from 'react'
import css from './New.module.css'

import { Switch, Route, Link } from 'react-router-dom'

const Form = (props: any) => {

  const if__val = window.location.pathname === '/renew/secure'
  const url = (if__val) ? '/renew/confirm__secure': '/renew/completed'
  const placeholder = (if__val) ? 'new secure key': 'confirm secure key'

  return (
    <div className={css.area}>
      <input className={css.inp} placeholder={placeholder} />
        <button className={css.submit}
                onClick={ () => props.history.push(url) }> next </button>
    </div>
  )
}

const Completed = () => {

  let answer = 'field'

  return (
    <div className={css.area}>
      <div className={css.answer}>
        <span> {answer} </span>
      </div>
      <Link to='/'>
        <button className={css.submit}> okey </button>
      </Link>
    </div>
  )
}

function NewSecureKey () {

  return (
    <Switch>
      <Route exact path='/renew/secure' render={Form} />
      <Route exact path='/renew/confirm__secure' render={Form} />
      <Route exact path='/renew/completed' render={Completed} />
    </Switch>
  )
}

export default NewSecureKey
