import React, { useState } from 'react'
import css from './Feed.module.css'

// import reload from '../../../media/reload_icon.svg'

import { Route, Redirect } from 'react-router-dom'
import Constructor from './Constructor/Template'

import io from 'socket.io-client'
const socket = io('http://localhost:1056')

function PublicFeed (props: any) {

  function Fresh () {

    const [ fresh, setFresh ]: any = useState([
      {type: 'history', text: 'Lorem ipsum dolor sit amet consectetur adipisicing elit.'},
      {type: 'history', text: 'Lorem ipsum dolor sit amet consectetur adipisicing elit.'},
      {type: 'short', text: 'Lorem ipsum dolor sit amet consectetur adipisicing elit.'}
    ])

    socket.emit('get fresh publications', null, function (publications: object) {

      setFresh(publications)
    })

    return <Constructor data={fresh} />
  }
  
  function Popular () {
    
    const [ hot, setHot]: any = useState([
      {type: 'history', text: 'Lorem ipsum dolor sit amet consectetur adipisicing elit.'}
    ])

    socket.emit('get hot publications', null, function (publications: object) {

      setHot(publications)
    })

    return <Constructor data={hot} />
  }

  return (
    <div className={css.area}>
        <Route exact path='/live'>
          <Redirect to='/live/fresh' />
        </Route>
        <div className={css.feed}>
          <Route exact path='/live/fresh' component={Fresh} />
          <Route exact path='/live/hot' component={Popular} />
          <Route exact path='/live/publication/:id'>
            <>
              <div>
                Publication Not Found
              </div>
              <button> reload </button>
            </>
          </Route>
          <Route>
            <Redirect to='/live/fresh' />
          </Route>
        </div>
    </div>
  )
}

export default PublicFeed
