import React from 'react'
import css from './Login.module.css'

import { Redirect } from 'react-router-dom'
import io from 'socket.io-client'

const socket = io('http://localhost:750')

function LOGIN (props: any) {
  
  type ref = any

  interface data {
    readonly 'user name': string,
    readonly 'user id': string,
    readonly 'secure key': string      
  }

  const name: ref = React.createRef()
  const secure: ref = React.createRef()

  const enter = () => {

    const [ val, length ]: [ string, number ] = [ name.current.value, name.current.value.length ]

    const $secure: string = secure.current.value

    const data: data = {
      'user name': val.slice(0, length - 5),
      'user id': val.slice(length - 3, length),
      'secure key': $secure
    }
    
    if (8 <= $secure.length) {
      
      socket.emit('enter', data, function (data: any) {

        if (data.auth === true) {

          localStorage.setItem('data', JSON.stringify(data) )

          props.history.push('/profile')

        } else {

            alert (' user name Or secure key not valid ')
          }

         localStorage.setItem('data', JSON.stringify(data) )
         return;

      })

    } else {

        alert('invalid data')
        console.log(secure.current.value.length);
        return;
      }


  }

  if (JSON.parse(localStorage.data).auth === 'true') {

    return <Redirect to='/profile' />

  } else {

    return (
      <div className={css.blank}>
        <div>
          <div className={css.form}>
            <input ref={name} className={`${css.name} ${css.inp}`}
                   type='text' placeholder='user name #000' />
            <input ref={secure} className={`${css.secure} ${css.inp}`}
                   type='password'  placeholder='secure key' />
          </div>
          <div className={css.buttons}>
            <button className={css.join}>
              <a href='http://join.shomi.ml'> Join </a>
            </button>
            <button className={css.LOGIN} onClick={enter}>
              <span> LOGIN </span>
            </button>
          </div>
          <button className={css.forgot}
                  onClick={ () => props.history.push('/forgot/upload') }>
            <span> forgot ? </span>
          </button>
        </div>
      </div>
    )

  }

}

export default LOGIN
