import React, { useState } from 'react'
import css from './Friend.module.css'

import { Link } from 'react-router-dom'
import io from 'socket.io-client'

const Item = (props: any) => {
  
  const [ data, setData ]: any = useState({
    name: null,
    FI: null,
    inbox: [],
  })

  const socket = io('http://localhost:15005')
  
  const getdata: any = {
    'method': 'get',
    '__id': props.data['__id'],
    'FI': props.data['FI']
  }
  
  if (data.name === null) {

    socket.emit('friends', getdata, function (reply: any) {
      
      console.log()
      setData(reply)
    })
  }
  
  return (
    <>
      <div className={css.friend}>
        <svg viewBox='0 0 120 120' className={css.icon}>
          <rect x='10' y='10' rx='20' ry='20' width='100' height='100'
                fill='rgba(255, 238, 0, 0.9)' />
          <text x="45" y="85" fill='#c3a720'
                style={{
                  fontFamily: 'Lemonada',
                  fontSize: '60px'
                }}> ? </text>
        </svg>
        <div className={css.data}>
          <div className={css.name}>
            <Link to='/profile/friends/to'>
              <span> { data.name } </span>
            </Link>
          </div>
          <button className={css.pref}> i </button>
        </div>
        <button className={css.delete}
                onClick={ () => props.onDelete(data['FI']) }>
          <span className={css.deleteInner}> delete </span>
        </button>
      </div>
      {
        (false) ? <hr /> : null
      }
    </>
  )
}

export default Item
