import React, { useState } from 'react'
import css from './Friends.module.css'

import { Helmet } from 'react-helmet'
import { Route } from 'react-router-dom'

import loader from '../../../media/loaderForFriends.svg'

import Friend from './Friend/Friend'
import To from './TO/To'

import io from 'socket.io-client'

const socket = io('http://localhost:15005')

const Feed = (props: any) => {

  const data = props.data

  function del (id: string) {
    
    const deldata = {
      'method': 'delete',
      '__id': JSON.parse(localStorage.data)['__id'],
      'FI': id
    }

    socket.emit('friends', deldata, function (reply: any) {

      window.location.reload()
    })
  }

  if (data !== null && data.length > 0) {
    
    return (
      <div className={css.friendsfeed}>
        {
          data.map( (friend: any) => {

              return <Friend data={friend} onDelete={ (v: string) => del(v) } key={friend['FI']} />
          })
        }
      </div>
    )

  } else if (data !== null && data.length === 0) {
    
    return (
      <span className={css.findfriends}>
        Maybe we'll
        <span className={css.find}
              onClick={ () => props.history.push('/socialize/choicer') }> find </span>
        <span className={css.a}> a </span> friend ?
      </span>
    )

  } else 
    return (
      <img className={css.loader} src={loader} alt='load' />
    )

}

function MyFriends (props: any) {
  
  const [ friends, setFriends ]: any = useState(null)

  const getdata = {
    '__id': JSON.parse(localStorage.data)['__id'],
    'keys': 'friends'
  }
  
  if (friends === null) {
    
    socket.emit('get', getdata, function (data: any) {

       setFriends(data)
    })

  }
  
  const input: any = React.useRef()
  
  const af = ($id: string) => {
    
    input.current.value = ''

    interface Data {
      'method': string,
      'FId': string,
      '__id': string
    }
   
    const data: any = {
      'method': 'add',
      'FI': $id,
      '__id': JSON.parse(localStorage.data)['__id']
    }

    socket.emit('friends', data, function (reply: any) {
      
      if (reply === true) {
        
        setFriends([ ...friends, reply ])
      } 
      
    })
    
  }

  return (
    <>
      <Helmet>
        <title> Friends&ensp;&ensp;Shomi </title>
      </Helmet>
      <Route exact path='/profile/friends'>
        <div className={css.area}>
          <div className={css.searchFriend}>
            <div className={css.itemSearchFriend}>
              <input ref={input} className={css.inp} 
                     type='text' placeholder='Friend ID  ?  e60YoOU2J35iOoOq' />
              <button className={css.add}
                      onClick={ () => af(input.current.value) }>
                <span className={css.innerAdd}> ADD </span>
              </button>
            </div>
          </div>
          <div className={css.friends}>
            <Feed data={friends} />
            <div className={css.footer}>
              <button className={css.toprofile} onClick={ () => props.history.push('/profile') }>
                <span> &lt; </span>
              </button>
              <div className={css.friends__amount}>
                <span className={css.amount}> { (friends !== null) ? friends.length: '0' } &nbsp;
                <span className={css.of}> of </span> &nbsp; 50 </span>
              </div>
            </div>
          </div>
        </div>
      </Route>
      <Route exact path='/profile/friends/to' render={To} />
    </>
  )
}

export default MyFriends
