import React, { ReactChild } from 'react'
import css from './SecureKey.module.css'

import { Switch, Route } from 'react-router-dom'

import NewSecure from './NewSecure/New'

const Default = (props: any): React.ReactChild => {

  return (
    <div className={css.area}>
      <label htmlFor='fileUpload' className={css.label}>
        <button className={css.selectfile}>
          <span className={css.select}> SELECT FILE </span>
        </button>
      </label>
      <input id='fileUpload' type="file" />
      <button className={css.upload}
              onClick={ () => props.history.push('/renew/secure') }>
         <span className={css.uploadInner}> UpLoad </span>
       </button>
    </div>
  )
}

function RenewSecureKey (props: any): React.ReactChild {

  return (
    <Switch>
      <Route path='/forgot/upload'
             render={Default} />

      <Route path='/forgot/secure'
             render={NewSecure} />

      <Route path='/forgot/confirm__secure'
             render={NewSecure} />

      <Route path='/forgot/done'
             render={NewSecure} />

    </Switch>
  )
}

export default RenewSecureKey
